a {
  text-decoration: none !important;

  .btnBody {
    height: 80px;
    width: 80px;
    border-radius: 20px;
    color: #f8d9c0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 5px auto;
    transition: all 0.2s ease;
    background: rgba(244, 187, 146, 0);
    &:hover {
      color: #fff;
    }
    .btnTile {
      margin: 4px auto;
      font-weight: 600;
      font-size: 12px;
    }
  }
  .active {
    background: rgb(146, 151, 244);
    color: #fff;
    transform: scale(1);
  }
}
